
import { defineComponent } from 'vue'
import { useI18n } from 'vue-i18n'

export default defineComponent({
  name: 'FlowerBoxSpec',
  emits: ["dataChanged"],
  data () {
      return {
          data: {
            line: "",
        }
      }
  },
  props: ["lines"],
  setup() {
    const { t } = useI18n({
      inheritLocale: true,
      useScope: 'local'
    });

    return { 
        t
    };
  },
  watch: {
    data: {
      handler: function (newVal) {
        this.$emit("dataChanged", newVal);
      },
      deep: true
    }
  }
})
